.rostik{
    width: 100%;
    height: 800px;
    background-color:blue;
    background-image: url("../../images/image_2021-08-23_09-55-06.png");
    background-size: 200px 100px;
    background-repeat: repeat;
    border: solid 1px brown;
    text-align: center;
}
.ros-h1{
    margin-top: 50px;
    text-align: center;
    background-color:orange;
    height: 50px;
    color: darkblue;
    vertical-align: middle;
    font-size:xx-large;
}
.ros-h2{
    background-color:orange;
    color: red;
}
.ros-h2 b{
    text-decoration: line-through;
    
    color: papayawhip;
}
.ros{
    margin-top: 20px;
    width: 200px;
    height: 100px;
    background-color: orange;
    color: darkblue;
}
.ros:hover {
    background-color: darkblue;
    color: orange;
    box-shadow: inset 0 0 0 3px #3a7999;
    animation: bounce 0.2s;
}
.ros-prize{
    margin-top: 20px;
}
@keyframes bounce {
	0%, 20%, 60%, 100% {
		transform: translateY(0);
		transform: translateY(0);
	}

	40% {
		transform: translateY(-20px);
		transform: translateY(-20px);
	}

	80% {
		transform: translateY(-10px);
		transform: translateY(-10px);
	}
}